.custom-pink {
  color: rgb(246 4 153);
}

.bg-custom-pink {
  background-color: rgb(246 4 153);
}

.img-overlay {
  position: relative;
}

.text-overlay {
  position: absolute;
  bottom: -50px;
  transition-duration: 500ms;
  color: rgba(255, 255, 255, 0);
}

.img-overlay:hover .text-overlay {
  color: rgba(255, 255, 255, 1);
  bottom: 0px;
}

.img-overlay .overlay-fade {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background-color: rgba(0, 0, 0, 0);
}

.img-overlay:hover .overlay-fade {
  background-color: rgba(0, 0, 0, 0.5);
  transition-duration: 500ms;
}

@media (max-width: 800px) {
  .event-list .col-lg-2 {
    text-align: center !important;
  }

  .event-list .col-lg-7 {
    text-align: center;
  }

  .event-list .col-lg-3 {
    text-align: center !important;
  }
}

.video-container {
  overflow: hidden;
  position: relative;
  width: 100%;
}

.video-container::after {
  padding-top: 56.25%;
  display: block;
  content: '';
}

.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.h-450 {
  min-height: 450px;
}

@media (max-width: 1300px) and (min-width: 800px) {
  .h-450 {
    min-height: 400px;
  }
}

.back-to-top {
  color: grey;
  border-radius: 50%;
}

.back-to-top:focus {
  border: none !important;
  outline: none;
}

.navbar a {
  color: inherit;
  text-decoration: none;
}

.navbar-mobile {
  display: none;
}

.navbar-mobile a {
  color: white !important;
}

@media (max-width: 1100px) {
  .navbar-web {
    display: none;
  }

  .navbar-mobile {
    display: flex;
  }
}

.bg-radio-show {
  background: url('./img/radioShowImage1.jpg') no-repeat center center/cover;
  min-height: 100vh;
}

.blur {
  -webkit-filter: blur(20px);
  filter: blur(20px);
}

.w-90 {
  width: 75%;
  margin-top: 65px;
  margin-left: auto;
  margin-right: auto;
}

.bg-dark {
  background-color: #181818 !important;
}

.after-radio-title {
  content: "";
  display: inline-block;
  left: 50%;
  margin-left: -12px;
  margin-bottom: -12px;
  width: 0;
  height: 0;
  bottom: 0;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
  border-top: 12px solid #181818;
  position: absolute;
}

.credits-7 a {
  text-decoration: none;
  color: #181818;
}